import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import { HeaderButton, List, Cell, Link, platform, CellButton, FormStatus, Header, SimpleCell, InfoRow, View, FixedLayout, Search, Separator, Tabs, TabsItem, IOS } from '@vkontakte/vkui';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';

import persik from '../img/persik.png';
import logo from '../img/logo.svg';
import './Persik.css';
import './Tochka.css';


const Home = ({ id, go, fetchedUser }) => (
	<Panel id={id} centered={true}>
				<PanelHeader
			left={<PanelHeaderButton onClick={go} data-to="home">
				{Home === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}
		>
			FAQ
		</PanelHeader>

                <Div style={{marginTop:70}}>

<Group header={<Header mode="secondary">Ответы на часто задаваемые вопросы</Header>}></Group>

</Div>


          <Div style={{ paddingTop: 60, paddingBottom: 60 }}>
            Для чего взымается оплата?<br/> — Оплата является офертой, подтверждающая, что Вы прочитали <Link onClick={go} data-to="rules">правила</Link>. <br/><p> Зачем нужна верификация? <br/>   — Мы не можем доверить определенную часть данных каждому желающему, именно по этому, после заполнения данных Ваш профиль пройдет автоматическую проверку на подлинность страницы.</p>
            <p> Как долго будет осуществляться выплата за выполненный check list? <br/> — Выплата может осуществляться 1-2 рабочих дней. </p> <p> Что такое чек лист? <br/> — Чек-лист (check list) – это перечень пошаговых последовательных действий, которые Вам необходимо выполнить, чтобы получить определенный результат.</p> <p>  Что делать после оплаты услуги верификации? <br/> — Убедитесь, что у Вас открыты личные сообщения, что бы мы могли оповестить Вас об изменении статуса аккаунта. </p> <p> Мне не поступало сообщений после оплаты, что делать? <br/> — Если Вас не оповестили об изменении статуса аккаунта в течении 12 часов, то просим Вас отправить скриншот чека об оплате нам в группу ВКонакте. </p>
            <p> Что мне делать после верификации? <br/>   — В разделе "Доступные задания" выберите интересующие Вас задания.</p>  <p> Я выбрал задание, что дальше? <br/>   — После нажатия ниже кнопки "Перейти к документации" необходимо скачать Word документ, в котором необходимо описать результаты тестирования.</p> <p> Я описал результаты тестирования, что дальше? <br/>   — Отправьте документ с результатами тестирования в личные сообщения группы ВКонтакте.</p>
            <p> Куда придут деньги за выполненный чек лист? <br/>   — После проверки документа с результатами тестирования с Вами свяжется Модератор.</p>
          </Div>
                        

            </Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
